.radio {
    appearance: none;
    height: 60px;
    width: 20px;

    cursor: pointer;
    border-radius: 50%;
    outline: none;
    background: #0811ba;

}

.radio:hover {
    border: 3px solid #ea0a98;
}

.radio:focus {
    border: 10px solid #d90909;
}

.label {
   cursor: pointer;

}

